import { createWebHistory, createRouter } from 'vue-router';
import Home from '@/views/Home.vue';
import About from '@/views/About.vue';
import Shop from '@/views/Shop.vue';
import Music from '@/views/Music.vue';

const routes = [
    {
        path: '/',
        name: 'Home',
        meta: {
            title: 'Bas Beelen | Composer and pianist'
        },
        component: Home,
    },
    {
        path: '/about',
        name: 'About',
        meta: {
            title: 'Bas Beelen | About'
        },
        component: About,
    },
    {
        path: '/shop',
        name: 'Shop',
        meta: {
            title: 'Bas Beelen | Shop'
        },
        component: Shop,
    },
    {
        path: '/music',
        name: 'Music',
        meta: {
            title: 'Bas Beelen | Music'
        },
        component: Music,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;