<template>
  <div class="row">
    <div class="col-1"></div>
    <div class="col-5">
      <div class="img_container">
        <!-- <image-zoom regular="../assets/abracadabra-page1.png"></image-zoom> -->
        <img src="../assets/abracadabra-page1.png" />
      </div>
    </div>
    <div class="col-5">
      <div>
        <div class="header">ABRACADABRA SHEET MUSIC</div>
        <ul>
          <li>4 pages, high quality digital copy (PDF)</li>
          <li>A4 format (210mm x 297mm)</li>
          <li>Personalized for you or someone else</li>
        </ul>
        <div>
          If you're interested in the Abracadabra sheet music, or you just want
          to support what I am up to, you can buy a digital copy by filling in
          the form below. I'll reach out on how to make the transaction.
        </div>
        <hr />
        <div>1x Abracadabra Sheet Music (PDF) <span>€ 5, -</span></div>
        <form @submit.prevent="buy" v-if="!submitted">
          <input
            type="text"
            required
            v-model="purchaseDetails.name"
            placeHolder="Your name"
          />
          <input
            type="text"
            required
            v-model="purchaseDetails.email"
            placeHolder="Your email address (to send the PDF)"
          />
          <input
            type="text"
            v-model="purchaseDetails.edition_for"
            placeHolder="Edition for (if different from your name)"
          />
          <input type="submit" :disabled="processing" value="Gimme" />
          <span class="error" v-if="errorMsg">{{ errorMsg }}</span>
        </form>
        <div v-if="submitted">
          <div class="check" v-if="submitted">&#10003;</div>
          <div class="thanks">Thanks for your interest, I'll get in touch!</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
// import imageZoom from "vue-image-zoomer";
export default {
  name: "Shop",
  data() {
    return {
      purchaseDetails: {
        name: null,
        email: null,
        edition_for: null,
      },
      submitted: false,
      processing: false,
      errorMsg: "",
    };
  },

  // components: {
  //   imageZoom,
  // },

  methods: {
    clear() {
      this.submitted = false;
      this.purchaseDetails.name = null;
      this.purchaseDetails.email = null;
      this.purchaseDetails.edition_for = null;
    },
    buy() {
      this.processing = true;
      axios
        .post(
          "https://europe-west1-basbeelen-com.cloudfunctions.net/shop",
          JSON.stringify(this.purchaseDetails),
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
        .then(() => {
          this.submitted = true;
          this.processing = false;
          setTimeout(this.clear, 5000);
        })
        .catch((error) => {
          this.errorMsg = error.response.data;
          setTimeout(() => {
            this.errorMsg = "";
            this.processing = false;
          }, 5000);
        });
    },
  },
};
</script>

